<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <el-breadcrumb separator-class="el-icon-arrow-right" class="my-breadcrumb">
        <el-breadcrumb-item :to="{ name: 'SetMealList' }">套餐维护</el-breadcrumb-item>
        <el-breadcrumb-item>{{id === 'insert' ? '新增' : '修改'}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container flex-item">
      <el-form size='small' class="default-form-style"
        label-width="auto"
        :model="formFields"
        :rules="rules"
        ref='form'>
        <el-row>
          <el-col :span="6">
            <el-form-item label="套餐名称：" prop="packName">
              <el-input 
                v-model="formFields.packName"
                maxlength="51"
                placeholder="请输入" />
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="套餐顺序：" prop="sortNum">
              <el-input 
                :value="formFields.sortNum"
                @input="$outInt($event, 'sortNum', formFields)"
                maxlength="5"
                placeholder="请输入" />
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="拼音简码：" prop="pySimpleCode">
              <el-input 
                v-model="formFields.pySimpleCode"
                maxlength="51"
                placeholder="请输入" />
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="五笔简码：" prop="wbSimpleCode">
              <el-input 
                v-model="formFields.wbSimpleCode"
                maxlength="51"
                placeholder="请输入" />
            </el-form-item>
          </el-col>
          <el-col :span="6">
            <el-form-item label="是否启用：">
              <el-select v-model="formFields.enabled">
                <el-option value="1" label="启用"></el-option>
                <el-option value="2" label="禁用"></el-option>
              </el-select>
            </el-form-item>
          </el-col>

          <el-col>
            <div class="flex" style="padding-top: 20px">
              <div class="left-box">
                <h4>选择项目</h4>
                <div class="item-box ">
                  <el-collapse>
                    <template v-for="item in projectList">
                      <el-collapse-item
                        :key='item.sysGuid'
                        v-if='item.smallProjects.length'>
                        <div slot="title" class="collapse-head" @click.stop>
                          <el-checkbox :label="item.sysGuid"
                            v-model="item.isAllSelect"
                            @change="handleAllSelect($event, item)"
                            :indeterminate='!!(item.ids.length && !item.isAllSelect)'>{{item.pname}}</el-checkbox>
                        </div>
                        <el-checkbox-group v-model="item.ids"
                          @change="handleSmallSelect($event, item)">
                          <el-checkbox
                            v-for="items in item.smallProjects"
                            :key='items.sysGuid'
                            :label="items.sysGuid">{{items.pname}}</el-checkbox>
                        </el-checkbox-group>
                      </el-collapse-item>
                    </template>
                    
                    <!-- <el-collapse-item name="2">
                      <div slot="title" style="padding-left:20px" @click.stop>
                        <el-checkbox>尿常规</el-checkbox>
                      </div>
                      <el-checkbox-group style="padding:10px 40px">
                        <el-checkbox label="尿蛋白"></el-checkbox>
                        <el-checkbox label="尿酮体"></el-checkbox>
                        <el-checkbox label="尿糖"></el-checkbox>
                        <el-checkbox label="尿潜血"></el-checkbox>
                      </el-checkbox-group>
                    </el-collapse-item>
                    <el-collapse-item name="3">
                      <div slot="title" style="padding-left:20px" @click.stop>
                        <el-checkbox>尿常规</el-checkbox>
