                    </el-collapse-item>
                    <el-collapse-item name="4">
                      <div slot="title" style="padding-left:20px" @click.stop>
                        <el-checkbox>尿常规</el-checkbox>
                      </div>
                      <el-checkbox-group style="padding:10px 40px">
                        <el-checkbox label="尿蛋白"></el-checkbox>
                        <el-checkbox label="尿酮体"></el-checkbox>
                        <el-checkbox label="尿糖"></el-checkbox>
                        <el-checkbox label="尿潜血"></el-checkbox>
                      </el-checkbox-group>
                    </el-collapse-item> -->
                  </el-collapse>
                </div>
              </div>
              <div class="arrow-box flex-v-center">
                <div class="iconfont bj-arrow-right"></div>
              </div>
              <div class="right-box">
                <h4>已选项目</h4>
                <div class="item-box">
                  <div class="table-box">
                    <el-table :data='checkedProjectList'
                      height="100%" 
                      header-cell-class-name='custom-table-cell'
                      stripe>
                      <el-table-column label='项目名称' 
                        prop="pname" 
                        width="120px"
                        align='center'></el-table-column>
                      <el-table-column label='小项名称' 
                        prop='smallProjects'
                        align='center'></el-table-column>
                    </el-table>
                  </div>
                </div>
                <!-- <dl class="item-box flex-v">
                  <dt class="flex">
                    <div class="index">序号</div>
                    <div class="flex flex-item">
                      <div class="dt-title">检查项目</div>
                      <div class="dt-title">项目名称</div>
                      <div class="dt-title">体检状态</div>
                      <div class="dt-title">数据传输</div>
                    </div>
                    <div class="last-item"></div>
                  </dt>
                  <dd class="flex-item">
                    <el-collapse>
                      <el-collapse-item v-for="(item, i) in checkedProjectList"
                        :key='i'>
