                          <div class="index">{{i + 1}}</div>
                          <div class="flex flex-item">
                            <div class="text-item">{{item.pname}}</div>
                          </div>
                          <div class="last-item"></div>
                        </template>
                        <div class="flex-v-center sub-item"
                          v-for="items in item.smallProjects"
                          :key='items.sysGuid'>
                          <div class="index"></div>
                          <div class="flex flex-item">
                            <div class="text-item">{{items.pname}}</div>
                            <div class="text-item">未完成</div>
                            <div class="text-item">未同步</div>
                          </div>
                          <div class="last-item"></div>
                        </div>
                      </el-collapse-item>
                    </el-collapse>
                  </dd>
                </dl> -->
                
              </div>
            </div>
          </el-col>
        </el-row>
      </el-form>
      <footer class="btn-box">
        <el-button class="btn-hollowing" @click="$router.push({name: 'SetMealList'})">取消</el-button>
        <el-button class="btn-info" @click="onSubmit" :loading="submitLoading">保存</el-button>
      </footer>
    </div>
  </div>
</template>

<script>
import { lenRule } from '@/lib/utils'

export default {
  name: 'SetMealForm',
  props: {
    id: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      projectList: [],
      formFields: {
        packName: '',
        pySimpleCode: '',
        wbSimpleCode: '',
        enabled: '1',
        sortNum: '',
        projectIds: ''
      },
      rules: {
        packName: [
          {
            required: true,
            message: '请输入套餐名称',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        sortNum: { required: true, message: '请输入套餐顺序', trigger: 'blur' },
        pySimpleCode: [
          {
            required: true,
            message: '请输入拼音简码',
            trigger: 'blur',
            whitespace: true
          },
          ...lenRule()
        ],
        wbSimpleCode: { max: 50, message: '最多输入50个字符', trigger: 'blur' }
      },
      submitLoading: false
    }
  },
  computed: {
    checkedProjectList() {
      return this.projectList
        .filter(item => item.ids.length)
        .map(item => {
          let smallProjects = item.smallProjects
            .filter(items => ~item.ids.indexOf(items.sysGuid))
            .map(item => item.pname)
          let newItem = {
            pname: item.pname,
            smallProjects: smallProjects.join('、')
          }
          return newItem
        })
    }
  },
  beforeRouteEnter(to, form, next) {
    if (to.params.id === 'insert') document.title = '套餐新增'
    next()
  },
  created() {
    this.initData(this.id)
  },
  methods: {
    initData(id) {
      id === 'insert' ? this.getAllProject() : this.getSetMealById(id)
    },

    getAllProject() {
      this.$post('/sys/project/getThisOrgAllProject')
        .then(({ data }) => {
          this.projectList = data
            .filter(item => item.smallProjects.length)
            .map(item => {
              item.isAllSelect = false
              item.ids = []
              return item
            })
        })
        .catch(err => this.$message.error(err))
    },

    getSetMealById(sysGuid) {
      this.$post('/sys/projectPackage/getBySysGuid', { sysGuid })
        .then(({ data }) => {
          let projects = data.thisOrgAllProjects,
            packBasic = data.packBasic,
            packChoice = data.packChoice.map(item => item.projectId)
          this.projectList = projects
            .filter(item => item.smallProjects.length)
            .map(item => {
              item.ids = []
              item.smallProjects.forEach(items => {
                if (~packChoice.indexOf(items.sysGuid))
                  item.ids.push(items.sysGuid)
              })
              return item
            })
            .map(item => {
              item.isAllSelect = item.ids.length === item.smallProjects.length
              return item
            })
          Object.keys(this.formFields).forEach(key => {
            this.formFields[key] = packBasic[key]
          })
          this.formFields.sysGuid = packBasic.sysGuid
          this.$nextTick(this.$refs.form.clearValidate)
        })
        .catch(err => err)
    },

    handleAllSelect(val, item) {
      item.ids = val ? item.smallProjects.map(item => item.sysGuid) : []
    },

    handleSmallSelect(val, item) {
      item.isAllSelect = val.length === item.smallProjects.length
    },

    onSubmit() {
      let url, msg
      if (this.id === 'insert') {
        msg = '新增'
        url = '/sys/projectPackage/insert'
      } else {
        msg = '修改'
        url = '/sys/projectPackage/update'
      }
      this.$refs.form.validate(valid => {
        if (!valid) return
        if (!this.handleIds()) return this.$message.error('至少选择一个项目')
        this.submitLoading = true
        this.$post(url, this.formFields)
          .then(res => {
            this.submitLoading = false
            this.$message.success(`${msg}成功`)
            this.$router.back() // 成功需拿到id跳转到查看页面
          })
          .catch(err => {
            this.submitLoading = false
            this.$message.error(err)
          })
      })
    },

    handleIds() {
      let ids = []
      this.projectList.forEach(item => {
        ids = ids.concat(item.ids)
      })
      console.log(ids)
      this.formFields.projectIds = ids.toString()
      return ids.length
    }
  }
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
}
.container {
  padding: 30px;
  overflow-y: auto;
  h4 {
    line-height: 1;
    font-size: 14px;
  }
  .body {
    margin-top: 30px;
  }

  .item-box {
    margin-top: 12px;
    width: 520px;
    height: 380px;
  }
  .left-box .item-box {
    border-radius: 6px;
    border: solid 1px #ccc;
    overflow-y: auto;
  }
  .arrow-box {
    width: 100px;
    .bj-arrow-right {
      margin: 0 auto;
      font-size: 36px;
      color: @color-red;
    }
  }
}
.table-box {
  padding: 0;
  height: 100%;
}

footer.btn-box {
  display: block;
  padding: 50px 0 20px;
  text-align: center;
}

.collapse-head {
  padding-left: 20px;
}
.el-collapse {
  border-top: 0;
  /deep/ .el-collapse-item__content {
    padding-bottom: 0;
  }
  /deep/ .el-collapse-item__arrow {
    font-size: 18px;
  }
  .el-checkbox-group {
    padding: 10px 20px 10px 40px;
  }
}
</style>


